<template>
  <div id="dashboard">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>Employees</h4>
      </li>
      <li v-for="employee in employees" v-bind:key="employee.id" class="collection-item">
        <div class="chip">{{employee.dept}}</div>
        {{employee.employee_id}}:{{employee.name}}

        <router-link class="secondary-content" v-bind:to="{name: 'view-employee' ,
        params:{employee_id: employee.employee_id}}">
        <i class="fa fa-eye"></i></router-link>
      </li>
    </ul>
    <div  class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-large red">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>
<script>

import getTCB from "./tcbaseinit"

export default {
  name:'dashboard',
  data () {
    return {
      employees: []
    }
  },
  async created() {
    try {
      let tcb = await getTCB()
    } catch (error) {
      window.console.log(error)
    }
      window.console.log("log in sucessful")
    let db = tcb.database()
    db.collection("employees").get().then((res) => {
      
      let data = res.data
      // console.log(data)
      for(let i =0;i<data.length;i++){
        this.employees.push(data[i])
      }
      
  })
}
}
</script>


