<template>
  <div id="edit-employee">
    <h3>Edit Employee</h3>
      <div class="row">
        <form @submit.prevent="updateEmployee" class="col s12">
          <div class="row">
            <div class="input-field col s12">
              <input disabled type="text" v-model="employee_id" required>
            </div>
          </div>
          <div class="row">
            <div class="input-field col s12">
              <input type="text" v-model="name" required>
            </div>
          </div>
          <div class="row">
            <div class="input-field col s12">
              <input type="text" v-model="dept" required>
            </div>
          </div>
          <div class="row">
            <div class="input-field col s12">
              <input type="text" v-model="position" required>
            </div>
          </div>
          <button type="submit" class="btn">Submit</button>
          <router-link to="/" class="btn grey">Cancel</router-link>
        </form>
      </div>
  </div>
</template>
<script>
import getTCB from "./tcbaseinit"

export default {
  name:'edit-employee',
  data(){
    return {
      employee_id:null,
      name: null,
      dept: null,
      position: null
    }
  },
  async beforeRouteEnter (to ,from ,next) {
    try {
      let tcb = await getTCB()
    } catch (error) {
      window.console.log(error)
    }
      window.console.log("log in sucessful")
    let db = tcb.database()
    db.collection("employees").where({
      employee_id: to.params.employee_id
    }).get()
    .then((res) => {
      console.log(res.data)
      next(vm =>{
        vm.employee_id = res.data[0].employee_id
        vm.name = res.data[0].name
        vm.dept = res.data[0].dept
        vm.position = res.data[0].position
        console.log(vm)
      })
  })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    async fetchData(){
      try {
        let tcb = await getTCB()
      } catch (error) {
        window.console.log(error)
      }
        window.console.log("log in sucessful")
      let db = tcb.database()
      db.collection("employees").where({
        employee_id: this.$route.params.employee_id
      }).get()
      .then(doc=> {
        console.log(doc)
          this.employee_id = doc.data[0].employee_id
          this.name = doc.data[0].name
          this.dept = doc.data[0].dept
          this.position = doc.data[0].position
      }
      )
    },
    async updateEmployee() {
        try {
        let tcb = await getTCB()
          } catch (error) {
            window.console.log(error)
          }
            window.console.log("log in sucessful")
          let db = tcb.database()
          tcb.callFunction({
                name: "employeeUpdate",
                data: { employee_id: this.employee_id, 
                        name : this.name,
                        dept: this.dept,
                        position: this.position}
            }).then(() => {
              this.$router.push({name:'view-employee',params:{
                employee_id:this.employee_id
              }})
            })
    }
  }
}
</script>


