<template>
  <div id="view-employee">
    <ul class="collection with-header">
      <li class="coleection-header">
        <h4>{{name}}</h4>
      </li>
      <li class="collection-item">Employee ID#:{{employee_id}}</li>
      <li class="collection-item">Department: {{dept}}</li>
      <li class="collection-item">Postion: {{position}}</li>
    </ul>
    <router-link to="/" class="btn grey">Back</router-link>
    <button @click="deletEmployee" class="btn red">Delet</button>

    <div  class="fixed-action-btn">
      <router-link v-bind:to = "{name:'edit-employee', params:{employee_id: employee_id}}" class="btn-floating btn-large red">
        <i class="fa fa-pencil"></i>
      </router-link>
    </div>
  </div>
</template>
<script>
import getTCB from "./tcbaseinit"
const aaa = []
export default {
  name:'view-employee',
  data(){
    return {
      employee_id:null,
      name: null,
      dept: null,
      position: null
    }
  },
  async beforeRouteEnter (to ,from ,next) {
    try {
      let tcb = await getTCB()
    } catch (error) {
      window.console.log(error)
    }
      window.console.log("log in sucessful")
    let db = tcb.database()
    db.collection("employees").where({
      employee_id: to.params.employee_id
    }).get()
    .then((res) => {
      console.log(res.data)
      next(vm =>{
        vm.employee_id = res.data[0].employee_id
        vm.name = res.data[0].name
        vm.dept = res.data[0].dept
        vm.position = res.data[0].position
        console.log(vm)
      })
  })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    async fetchData(){
      try {
        let tcb = await getTCB()
      } catch (error) {
        window.console.log(error)
      }
        window.console.log("log in sucessful")
      let db = tcb.database()
      db.collection("employees").where({
        employee_id: this.$route.params.employee_id
      }).get()
      .then(doc=> {
        console.log(doc)
          this.employee_id = doc.data[0].employee_id
          this.name = doc.data[0].name
          this.dept = doc.data[0].dept
          this.position = doc.data[0].position
      }
      )
    },
    async deletEmployee() {
      if(confirm('Are you sure?')){
        try {
        let tcb = await getTCB()
          } catch (error) {
            window.console.log(error)
          }
            window.console.log("log in sucessful")
          let db = tcb.database()
          db.collection("employees").where({
            employee_id: this.$route.params.employee_id
          }).remove(console.log('123')).then(res=>{
            tcb.callFunction({
                name: "employeeDelete",
                data: { employee_id: this.$route.params.employee_id }
            }).then(() => {
              this.$router.push('/')
            })
          })
      }
    }
  }
}
</script>


